<template>
  <v-card>
    <v-toolbar
      dark
      color="indigo"
      class="font-weight-bold"
      flat
    >
      
      <v-spacer></v-spacer>
      <v-btn
        icon
        @click="memberSave"
      >
        <v-icon>fa-save</v-icon>
      </v-btn>
      <v-btn
        icon
        @click="finish"
      >
        <v-icon>fa-undo</v-icon>
      </v-btn>
    </v-toolbar>
    <v-card-text>
      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
      >
        <v-container>
          <v-row>
            <v-col>
              <v-text-field
                v-model="member.id"
                :counter="20"
                :rules="idRules"
                label=""
                required
                dense
                disabled
              ></v-text-field>
            </v-col>
            <v-col>
              <v-select
                v-model="member.type"
                :items="typeItems"
                item-text="text"
                item-value="value"
                :rules="[v => !!v || '  .']"
                label=""
                required
                dense
              ></v-select>
            </v-col>
          </v-row>

          <v-row>
            <v-col>
              <v-text-field
                v-model="member.pass"
                :counter="20"
                :rules="passRules"
                label=""
                required
                type="password"
                dense
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                v-model="member.passConfirm"
                :counter="20"
                :rules="passConfirmRules"
                label=" "
                required
                type="password"
                dense
              ></v-text-field>
            </v-col>
          </v-row>

          <v-row>
            <v-col>
              <v-text-field
                v-model="member.name"
                :counter="30"
                :rules="nameRules"
                label="  "
                required
                dense
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                v-model="member.email"
                :rules="emailRules"
                label=""
                required
                dense
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                v-model="member.tel"
                :rules="telRules"
                label=""
                dense
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                v-model="member.fax"
                :rules="telRules"
                label=""
                dense
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                v-model="member.phone"
                :rules="telRules"
                label=""
                dense
              ></v-text-field>
            </v-col>
          </v-row>

          <v-row>
            <v-col>
              <v-row>
                <v-col
                  cols="12"
                  sm="8"
                >
                  <v-text-field
                    v-model="member.zipCode"
                    label=""
                    dense
                    readonly
                    @focus="openZippop"
                  ></v-text-field>
                </v-col>
                <v-col
                  cols="12"
                  sm="4"
                  class="pl-0"
                >
                  <v-btn
                    color="primary"
                    @click.stop="openZippop"
                  >
                    
                  </v-btn>
                </v-col>
              </v-row>
            </v-col>
            <v-col>
              <v-text-field
                v-model="member.addr1"
                label=""
                dense
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                v-model="member.addr2"
                label=""
                dense
              ></v-text-field>
            </v-col>
          </v-row>

          <v-row>
            <v-col>
              <v-text-field
                v-model="member.nation"
                label=""
                dense
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                v-model="member.area1"
                label="1"
                dense
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                v-model="member.area2"
                label="2"
                dense
              ></v-text-field>
            </v-col>
          </v-row>

          <v-row>
            <v-col>
              <v-text-field
                v-model="member.companyNo"
                label=""
                dense
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                v-model="member.ceoNm"
                label=""
                dense
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                v-model="member.corpNo"
                label=""
                dense
              ></v-text-field>
            </v-col>
          </v-row>
        </v-container>
      </v-form>
    </v-card-text>
  </v-card>
</template>
<script>
export default {
  props: [ 'member' ],
  name: 'ProfileEdit',
  data() {
    return {
      valid: true,
      idRules: [
        v => !!v || '()  .',
        v => (v && v.length >= 5) || '  5  .',
        v => (v && v.length <= 20) || '  20   .',
      ],
      passRules: [
        v => !!v || '()  .',
        v => (v && v.length >= 8) || '  8  .',
        v => (v && v.length <= 20) || '  20   .',
      ],
      passConfirmRules: [
        v => (v && v === this.member.pass) || '  .',
      ],
      nameRules: [
        v => !!v || '  ()  .',
        v => (v && v.length >= 2) || '  2  .',
        v => (v && v.length <= 30) || '  ()  30   .',
      ],
      emailRules: [
        v => !!v || 'E-mail()  .',
        v => /.+@.+\..+/.test(v) || '   .',
      ],
      telRules: [
        v => (!v || /\d{2,3}-\d{3,4}-\d{4}/.test(v)) || '   .',
      ],
      typeRules: [
        v => !!v || '  .'
      ],
      typeItems: [
        { text: '', value: 'M' },
        { text: '', value: 'C' },
        { text: '', value: 'P'}
      ]
    }
  },
  methods: {
    finish() {
      this.$emit('finish');
    },
    memberSave() {
      if(this.$refs.form.validate()) {
        this.$store.dispatch('user/save', { memberInfo: this.member })
            .then(() => this.finish());
      }
    },
    openZippop() {
      const self = this.member;
      new window.daum.Postcode({
        oncomplete: function(data) {
          self.zipCode = data.zonecode;
          const address = data.buildingName ? `${data.address} (${data.buildingName})` : data.address;
          self.addr1 = address;
        }
      }).open();
    }
  }
}
</script>